import { commonParams, options } from './config'
import Vue from 'vue'
export function getSortDetail(keywords, configId, drugFormId, brandId, otcId, promotionId, pageNo, pageSize) {
  const url = 'api/drug/drugAcvityList.htm'
  const params = Object.assign({}, commonParams, {
    'keywords': keywords,
    'configId': configId,
    'drugFormId': drugFormId,
    'brandId': brandId,
    'otcId': otcId,
    'promotionId': promotionId,
    'pageNo': pageNo,
    'pageSize': pageSize
  })
  return Vue.http.post(url, params, options).then(
    (successResponse) => {
      return Promise.resolve(successResponse.body)
    },
    (errorResponse) => {
      return Promise.reject(errorResponse)
    }
  )
}



