<template>
  <div class="header1">
    <div class="left-wrapper" v-if="showBackIcon">
      <span class="left-icon"></span>
    </div>
    <div class="title-wrapper">
      <span class="title">{{title}}</span>
    </div>
    <div class="right-wrapper">
      <span class="right-icon" :class="rightIcon">{{rightText}}</span>
    </div>
    <oneline class="oneline"></oneline>
  </div>
</template>

<script type="text/ecmascript-6">
  import Oneline from 'base/split/1pxline.vue'
  export default {
    props: {
      title: {
        type: String,
        default: ''
      },
      showBackIcon: {
        type: Boolean,
        default: true
      },
      rightIcon: {
        type: String,
        default: ''
      },
      rightText: {
        type: String,
        default: ''
      }
    },
    components: {
      Oneline
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .header1
    left: 0
    top: 0
    width: 100%
    z-index: 50
    height: 40px
    background: #F6F7F7
    position: relative
    .left-wrapper
      position: absolute
      left: 0
      top: 0
      height: 39px
      width: 39px
      display: flex
      align-items: center
      justify-content: center
      .left-icon
        width: 10px
        height: 14px
        display: inline-block
        background-size: 10px 14px
        background-repeat: no-repeat
        bg-image('pharmacy_blue_back')
    .right-wrapper
      position: absolute
      right: 0
      top: 0
      height: 39px
      width: 39px
      display: flex
      align-items: center
      justify-content: center
      .right-icon
        width: 40px
        height: 20px
        display: inline-block
        background-size: 20px 20px
        background-repeat: no-repeat
        background-position: center
        color: #00A5F9
        font-size: 15px
        text-align: center
        line-height: 20px
        &.pharmacy_search
          bg-image('pharmacy_search')
    .title-wrapper
      width: 100%
      height: 39px
      display: flex
      justify-content: center
      align-items: center
      .title
        font-size: 18px
        line-height: 39px
    .oneline
      position: absolute
      bottom: 0

</style>



