import * as type from './mutation-types'

const mutations = {
  [type.SET_DRUG](state, drug) {
    state.drug = drug
  },
  [type.SET_SHOPPING_CAR_LIST](state, shoppingCarList) {
    state.shoppingCarList = shoppingCarList
  },
  [type.SET_IS_ALL_SELECTED](state, isAllSelected) {
    state.isAllSelected = isAllSelected
  }
}

export default mutations



