<template>
  <div class="double">
    <div class="left-wrapper">
      <ul>
        <li v-for="(item,index ) in filterData.showData" class="left-menu" :class="{'current':currentIndex1 === index}"
            @click="clickCurrentIndex(index)">
          <div>
            <span>{{item.name}}</span>
          </div>
        </li>
      </ul>

    </div>
    <div class="right-wrapper" ref="scrollWrapper">
      <ul>
        <li v-for="(item,index ) in filterData.showData[currentIndex1].data" class="item-data-wrapper"
            @click="clickItemAction(item,index)" :class="{'selected':selectPosition1===index}">
          <div>
            <span>{{item.key}}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'
  //  import Vue from 'vue'
  const TAG = 'Double...'
  export default {
    data() {
      console.log(TAG + 'data()')
      return {
        currentIndex1: this.currentIndex,
        selectPosition1: this.selectPosition,
        saveIndexPosition: [0, 0]
      }
    },
    props: {
      filterData: {
        type: Object
      },
      filterPosition: {
        type: Number
      },
      selectPosition: {
        type: Number,
        default: 0
      },
      currentIndex: {
        type: Number,
        default: 0
      }
    },
    methods: {
      clickItemAction(item, index) {
        this.saveIndexPosition[this.currentIndex1] = index
        this.selectPosition1 = index
        this.$emit('clickItemAction', item, index, this.filterData.showType, this.filterPosition, this.currentIndex1)
      },
      clickCurrentIndex(index) {
        console.log(TAG + 'clickCurrentIndex')
        this.currentIndex1 = index
        this.selectPosition1 = this.saveIndexPosition[this.currentIndex1]
        this.$emit('clickItemAction', this.filterData.showData[this.currentIndex1].data[this.selectPosition1], this.selectPosition1, this.filterData.showType, this.filterPosition, this.currentIndex1, true)
        this._initScroll()
      },
      _initScroll() {
        console.log(TAG + '_initScroll')
        this.$nextTick(() => {
          if (!this.dataScroll) {
            console.log(TAG + '_initScroll==NULL')
            this.dataScroll = new BScroll(this.$refs.scrollWrapper, {
              click: true
            })
          } else {
            console.log(TAG + '_initScroll!=NULL')
            this.dataScroll.refresh()
          }
        })
      }
    },
//    computed: {
//      getCurrentIndex() {
//        this._initScroll()
//        console.log(TAG + 'getCurrentIndex   ' + this.currentIndex)
//        return this.currentIndex
//      },
//      getScrollDataList() {
//        this._initScroll()
//        console.log(TAG + 'getScrollDataList')
//        console.log(TAG + 'getScrollDataList   ' + this.currentIndex)
//        return this.showData[this.currentIndex].data
//      }
//    },
    beforeCreate() {
      console.log(TAG + 'beforeCreate')
    },
    created() {
      console.log(TAG + 'created')
      this._initScroll()
    },
    beforeMount() {
      console.log(TAG + 'beforeMount')
    },
    mounted() {
      console.log(TAG + 'mounted')
    },
    beforeUpdate() {
      console.log(TAG + 'beforeUpdate')
    },
    updated() {
      console.log(TAG + 'updated')
    },
    activated() {
      console.log(TAG + 'activated')
    },
    deactivated() {
      console.log(TAG + 'deactivated')
    },
    beforeDestroy() {
      console.log(TAG + 'beforeDestroy')
    },
    destroyed() {
      console.log(TAG + 'destroyed')
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .double
    display: flex
    background: #fff
    width: 100%
    height: 300px
    .left-wrapper
      flex: 0 0 100px
      background: #F0F0F0
      .left-menu
        border-1px(#DBD8D8, 0)
        text-align: center
        height: 40px
        line-height: 40px
        &.current
          background: #fff
    .right-wrapper
      flex: 1
      position: relative
      overflow: hidden
      .item-data-wrapper
        position: absolute
        left: 10px
        height: 40px
        line-height: 40px
        border-1px(#DBD8D8, 0)
        &.selected
          color: #0294DF

</style>



