<template>
  <div>
    <div class="single">
      <ul>
        <li v-for="(item ,index) in filterData.showData" class="single-content-wrapper"
            @click="clickItemAction(item, index)" :class="{'selected':selectPosition1===index}">
          <div class="single-div">
            <span>{{item.key}}</span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      filterData: {
        type: Object
      },
      filterPosition: {
        type: Number
      },
      selectPosition: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        selectPosition1: this.selectPosition
      }
    },
//    watch: {
//      selectPosition1() {
//        console.log('ddddddddddddddddd')
//      }
//    },
    methods: {
      clickItemAction(item, index) {
        this.selectPosition1 = index
        this.$emit('clickItemAction', item, index, this.filterData.showType, this.filterPosition)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .single
    background: #fff
    width: 100%
    height: 200px
    .single-content-wrapper
      position: relative
      width: 100%
      height: 40px
      line-height: 40px
      border-1px(#DBD8D8, 0)
      &.selected
        color: #0294DF
      .single-div
        left: 50px
        position: absolute
</style>



