<template>
  <div>
    <header1 :title="''" :showBackIcon="false" :rightIcon="'pharmacy_search'"></header1>
    <div class="drugsort">
      <div class="left-menu-wrapper">
        <ul>
          <li v-for="(item,index) in levelOne" class="item-left-menu" :class="{'current':currentPosition === index}"
              @click="_selectRightMenu(index)">
            {{item.cateName}}
          </li>
        </ul>
      </div>
      <div class="right-content-wrapper">
        <scroll ref="scroll" class="scroll-content" :data="levelTwo"
                @scrollToEnd="searchMore" :pullup="true">
          <div>
            <ul>
              <li v-for="(bigItem,bigIndex) in levelTwo">
                <div class="item-right-content">
                  <div class="title">{{bigItem.cateName}}</div>
                  <oneline></oneline>
                  <div class="content">
                    <ul class="ul-content" v-for="parentIndex in Math.ceil(bigItem.sonCateInfos.length/3)">
                      <li v-for="(item,index) in _getRowItemFromLevelDetail(parentIndex-1,bigItem.sonCateInfos)"
                          class="li-content">
                        <div class="div-content" v-if="item">
                          <img :src="item.imagePath" width="50" height="55"/>
                          <div class="title" :class="{'short':item.cateName.length <= 4}">{{item.cateName}}</div>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </li>
              <loading v-show="hasMore" title=""></loading>
            </ul>
          </div>
        </scroll>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Oneline from 'base/split/1pxline.vue'
  import Scroll from 'base/scroll/scroll'
  import Header1 from 'components/header/header1'
  import { getLevelOne, getLevelDetail } from 'api/drugsort'
  import Loading from 'base/loading/loading'
  const TAG = 'drugsort '
  const LOAD_SIZE = 3
  export default {
    data() {
      return {
        levelOne: [],
        levelTwo: [],
        currentPosition: 0,
        hasMore: true
      }
    },
    created() {
      console.log(TAG + 'created')
      this._getLevelOne()
    },
    methods: {
      _getLevelOne() {
        getLevelOne().then((response) => {
          if (response.code === 1) {
            this.levelOne = response.data
            console.log(TAG + this.levelOne.length)
            if (this.levelOne && this.levelOne.length > 0) {
              this._getLevelDetail(this.levelOne[0].cateId, 0, LOAD_SIZE)
            }
          }
        })
      },
      _getLevelDetail(cateId, start, length) {
        getLevelDetail(cateId, start, length).then((response) => {
          if (response.code === 1) {
            this.levelTwo = response.data
            this._checkMore(response.data)
          }
        })
      },
      _selectRightMenu(index) {
        this.currentPosition = index
        this._getLevelDetail(this.levelOne[index].cateId, 0, LOAD_SIZE)
      },
      _getRowItemFromLevelDetail(row, currentList) {
        let classes = []
        for (let i = row * 3; i < (row + 1) * 3; i++) {
          classes.push(currentList[i])
        }
        return classes
      },
      _checkMore(data) {
        if (data && data.length === LOAD_SIZE) {
          this.hasMore = true
        } else {
          this.hasMore = false
        }
      },
      searchMore() {
        if (!this.hasMore) {
          return
        }
        getLevelDetail(this.levelOne[this.currentPosition].cateId, this.levelTwo.length, LOAD_SIZE).then((response) => {
          if (response.code === 1) {
            this.levelTwo = this.levelTwo.concat(response.data)
            this._checkMore(response.data)
          }
        })
      }
    },
    components: {
      Oneline,
      Scroll,
      Header1,
      Loading
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .drugsort
    display: flex
    position: fixed
    top: 40px
    left: 0
    bottom: 48px
    width: 100%
    .left-menu-wrapper
      flex: 0 0 80px
      background: #f4f4f4
      .item-left-menu
        height: 48px
        width: 100%
        display: flex
        align-items: center
        justify-content: center
        background: #f4f4f4
        color: #4B4A4A
        &.current
          background: #fff
          color: #00A5F9
    .right-content-wrapper
      flex: 1
      position: relative
      .scroll-content
        overflow: hidden
        padding: 0 10px
        position: absolute
        top: 0
        bottom: 0
        right: 0
        left: 0
        .item-right-content
          .title
            font-size: 15px
            line-height: 15px
            color: #747474
            padding: 8px 0
          .content
            padding: 8px 0
            .ul-content
              display: flex
              .li-content
                flex: 1
                .div-content
                  display: flex
                  height: 100px
                  width: 100%
                  justify-content: center
                  align-items: center
                  flex-direction: column
                  .title
                    font-size: 10px
                    line-height: 10px
                    height: 10px
                    width: 60px
                    white-space: nowrap
                    overflow: hidden
                    text-overflow: ellipsis
                    &.short
                      display: flex
                      justify-content: center


</style>



