<template>
  <div class="header2">
    <div class="left-wrapper" v-if="showBackIcon" @click="back">
      <span class="left-icon"></span>
    </div>
    <div class="search-wrapper">
      <span class="search-icon"></span><input class="input-text" placeholder=""/>
    </div>
    <div class="right-wrapper">
      <span class="right-icon" :class="rightIcon"></span>
    </div>
    <oneline class="oneline"></oneline>
  </div>
</template>

<script type="text/ecmascript-6">
  import Oneline from 'base/split/1pxline.vue'
  export default {
    props: {
      showBackIcon: {
        type: Boolean,
        default: true
      },
      rightIcon: {
        type: String,
        default: 'pharmacy_titlebar_icon_point_normal'
      }
    },
    components: {
      Oneline
    },
    methods: {
      back() {
        this.$router.back()
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .header2
    left: 0
    top: 0
    width: 100%
    z-index: 50
    height: 40px
    background: #F6F7F7
    position: relative
    .left-wrapper
      position: absolute
      left: 0
      top: 0
      height: 39px
      width: 39px
      display: flex
      align-items: center
      justify-content: center
      .left-icon
        width: 10px
        height: 14px
        display: inline-block
        background-size: 10px 14px
        background-repeat: no-repeat
        bg-image('pharmacy_blue_back')
    .right-wrapper
      position: absolute
      right: 0
      top: 0
      height: 39px
      width: 39px
      display: flex
      align-items: center
      justify-content: center
      .right-icon
        width: 40px
        height: 20px
        display: inline-block
        background-size: 20px 20px
        background-repeat: no-repeat
        background-position: center
        color: #00A5F9
        font-size: 15px
        text-align: center
        line-height: 20px
        &.pharmacy_titlebar_icon_point_normal
          bg-image('pharmacy_titlebar_icon_point_normal')
    .search-wrapper
      position: absolute
      left: 39px
      right: 39px
      padding: 3px
      margin: 5.5px 0
      background: #ffffff
      -webkit-border-radius: 3px
      -moz-border-radius: 3px
      border: 1px solid #878787
      .search-icon
        width: 16px
        height: 16px
        display: inline-block
        background-size: 16px 16px
        background-repeat: no-repeat
        background-position: center
        bg-image('pharmacy_sousuo')
      .input-text
        position: absolute
        display: inline-block
        margin-left: 5px
        outline: none
        font-size: 15px
        width: 80%
    .oneline
      position: absolute
      bottom: 0
</style>



