<template>
  <div>
    <div class="hos-filter">
      <div class="title-wrapper">
        <ul class="ul-title-wrapper">
          <li v-for="(item, index) in filters" class="li-filter-title"
              :class="{'current':filters.length-1 === index}">
            <div class="div-title-wrapper" @click="clickCurrentIndex(item, index)">
              <span>{{item.bigTitle}}</span>
            </div>
          </li>
        </ul>
      </div>
      <div>
        <keep-alive>
          <component :is='currentView' v-show="showFilterDetail" :filterData="filters[currentIndex]"
                     :filterPosition="currentIndex"
                     v-on:clickItemAction="clickItemAction">
          </component>
        </keep-alive>
      </div>
    </div>
    <div class="filter-mask" v-show="showFilterDetail" @click="hideFilterContent">
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import FilterDouble from './Double.vue'
  import FilterSingle from './Single.vue'
  import FilterHybrid from './Hybrid.vue'
  import {bus} from 'common/js/bus'
  const ERR_OK = 0
  const DOUBLE_TYPE = 'double'
  const SINGLE_TYPE = 'single'
  const HYBRID_TYPE = 'hybrid'
  const TAG = 'HosFilter...'

  // 
  let single1 = {extends: FilterSingle}
  let single2 = {extends: FilterSingle}

  export default {
    data() {
      console.log(TAG + 'data()')
      return {
        currentIndex: 0,
        currentView: null,
        showFilterDetail: false,
        saveSelectPosition: [],
        doubleSelectIndex: 0
      }
    },
    props: {
      filters: {
        type: Array,
        default() {
          return [
            {
              showType: DOUBLE_TYPE,
              bigTitle: '',
              showData: [
                {
                  name: '',
                  data: [
                    {key: '', value: ''}
                  ]
                },
                {
                  name: '',
                  data: [
                    {key: '', value: ''},
                    {key: '1000', value: '1'},
                    {key: '3000', value: '3'},
                    {key: '5000', value: '5'},
                    {key: '10000', value: '10'},
                    {key: '', value: '999'}
                  ]
                }
              ]
            },
            {
              showType: SINGLE_TYPE,
              bigTitle: '',
              showData: [
                {key: '', value: ''}
              ]
            },
            {
              showType: HYBRID_TYPE,
              bigTitle: '',
              showData: [
                {key: '', value: ''},
                {key: '', value: '1'}
              ]
            },
            {
              showType: SINGLE_TYPE,
              bigTitle: '',
              showData: [
                {key: '', value: '1'},
                {key: '', value: '2'},
                {key: '', value: '3'}
              ]
            }
          ]
        }
      }
    },
    components: {
      FilterDouble,
      FilterSingle,
      FilterHybrid
    },
    methods: {
      clickItemAction(item, index, showType, filterPosition, currentIndex, isClickCurIndex) {
//        console.log(TAG + 'key:' + item.key)
//        console.log(TAG + 'value:' + item.value)
//        console.log(TAG + 'index:' + index)
//        console.log(TAG + 'showType:' + showType)
//        console.log(TAG + 'filterPosition:' + filterPosition)
//        console.log(TAG + 'currentIndex:' + currentIndex)
//        this.saveSelectPosition[filterPosition] = index
//        if (filterPosition === 0) {
//          this.doubleSelectIndex = currentIndex
//        }
        if (!isClickCurIndex) {
          this.hideFilterContent()
        }
        bus.$emit('params-select', item, index, showType, filterPosition, currentIndex)
      },
      clickCurrentIndex(item, index) {
        if (this.currentIndex === index) {
          if (this.showFilterDetail) {
            this.showFilterDetail = false
          } else {
            this.showFilterDetail = true
          }
        } else {
          if (!this.showFilterDetail) {
            this.showFilterDetail = true
          }
        }
        this.currentIndex = index
        if (item.showType === DOUBLE_TYPE) {
          this.currentView = FilterDouble
        } else if (item.showType === SINGLE_TYPE) {
          if (index === 1) {
            this.currentView = single1
          } else {
            this.currentView = single2
          }
          console.log(single1 === single2)
        } else if (item.showType === HYBRID_TYPE) {
          this.currentView = FilterHybrid
        }
      },
      hideFilterContent() {
        this.showFilterDetail = false
      }
    },
    computed: {
//      showCurrentFilterContent() {
//        return this.currentIndex
//      }
    },
    beforeCreate() {
      console.log(TAG + 'beforeCreate')
    },
    created() {
      console.log(TAG + 'created')
      this.$http.get('api/common/dict.htm', {
        params: {
          dict_name: 'distict,hosplevel',
          dict_inputval: '410200, '
        }
      }).then((response) => {
        response = response.body
        if (response.code === ERR_OK) {
          response.dicts.distict.forEach((dis) => {
            dis = {key: dis.disp_name, value: dis.dict_id}
            this.filters[0].showData[0].data.push(dis)
          })
          response.dicts.hosplevel.forEach((hos) => {
            hos = {key: hos.disp_name, value: hos.dict_id}
            this.filters[1].showData.push(hos)
          })
          console.log(TAG + 'distict.length=' + this.filters[0].showData[0].data.length)
        }
      })
    },
    beforeMount() {
      console.log(TAG + 'beforeMount')
    },
    mounted() {
      console.log(TAG + 'mounted')
    },
    beforeUpdate() {
      console.log(TAG + 'beforeUpdate')
    },
    updated() {
      console.log(TAG + 'updated')
    },
    activated() {
      console.log(TAG + 'activated')
    },
    deactivated() {
      console.log(TAG + 'deactivated')
    },
    beforeDestroy() {
      console.log(TAG + 'beforeDestroy')
    },
    destroyed() {
      console.log(TAG + 'destroyed')
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .hos-filter
    position: absolute
    width: 100%
    top: 40px
    z-index: 100
    .title-wrapper
      width: 100%
      height 40px
      background: #fff
      border-bottom: 1px solid #DBD8D8
      .ul-title-wrapper
        display: flex
        .li-filter-title
          height: 40px
          flex: 1
          text-align: center
          line-height: 40px
          border-1px-horizontal(#DBD8D8, 9px)
          &.current
            border-none()
        .div-title-wrapper
          margin: auto

  .filter-mask
    position: absolute
    width: 100%
    bottom: 0
    top: 40px
    z-index: 50
    background: rgba(7, 17, 27, 0.8)
</style>



