<template>
  <div class="tab">
    <div class="tab-item" @click="_OnClickCurrentLink(0)">
      <router-link to="/onlinepharmacy">
        <div class="tab-item-wrapper">
          <span v-show="mCurrentIndex===0" class="icon check0"></span>
          <span v-show="mCurrentIndex!==0" class="icon unCheck0"></span>
          <span class="title"></span>
        </div>
      </router-link>
    </div>
    <div class="tab-item" @click="_OnClickCurrentLink(1)">
      <router-link to="/drugsort">
        <div class="tab-item-wrapper">
          <span v-show="mCurrentIndex===1" class="icon check1"></span>
          <span v-show="mCurrentIndex!==1" class="icon unCheck1"></span>
          <span class="title"></span>
        </div>
      </router-link>
    </div>
    <div class="tab-item" @click="_OnClickCurrentLink(2)">
      <router-link to="/demandlist">
        <div class="tab-item-wrapper">
          <span v-show="mCurrentIndex===2" class="icon check2"></span>
          <span v-show="mCurrentIndex!==2" class="icon unCheck2"></span>
          <span class="title unCheck2"></span>
        </div>
      </router-link>
    </div>
    <div class="tab-item" @click="_OnClickCurrentLink(3)">
      <router-link to="/ordermanager">
        <div class="tab-item-wrapper">
          <span v-show="mCurrentIndex===3" class="icon check3"></span>
          <span v-show="mCurrentIndex!==3" class="icon unCheck3"></span>
          <span class="title"></span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        mCurrentIndex: 0
      }
    },
    methods: {
      _OnClickCurrentLink(index) {
        this.mCurrentIndex = index
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .tab
    display: flex
    position: fixed
    left: 0
    bottom: 0
    width: 100%
    height: 48px
    background: #f4f4f4
    .tab-item
      flex: 1
      & > a
        font-size: 10px
        color: rgb(77, 85, 93)
        &.router-link-active
          color: #00A5F9
      .tab-item-wrapper
        display: flex
        width: 100%
        height: 48px
        flex-direction: column
        justify-content: center
        align-items: center
        .icon
          display: inline-block
          width: 23px
          height: 23px
          background-size: 23px 23px
          background-repeat: no-repeat
          &.check0
            bg-image('pharmacy_pocket_pharmacies_selected')
          &.unCheck0
            bg-image('pharmacy_pocket_pharmacies')
          &.check1
            bg-image('pharmacy_classification_selected')
          &.unCheck1
            bg-image('pharmacy_classification')
          &.check2
            bg-image('pharmacy_list_selected')
          &.unCheck2
            bg-image('pharmacy_list')
          &.check3
            bg-image('pharmacy_order_selected')
          &.unCheck3
            bg-image('pharmacy_order')
        .title
          margin-top: 4px
</style>



