<template>
  <transition name="slide">
    <div class="sort-detail">
      <header2></header2>
      <hos-filter></hos-filter>
      <scroll ref="scroll" class="drug-list" :data="drugList" @scrollToEnd="searchMore" :pullup="true">
        <div>
          <ul>
            <li v-for="(item,index) in drugList" class="drug-info-wrapper">
              <div class="avatar-wrapper">
                <img v-lazy="item.imagePath" width="60" height="80">
              </div>
              <div class="content-wrapper">
                <h1 class="title">{{item.standardName}}</h1>
                <p class="des">{{item.indications}}</p>
                <div class="extra-content">
                  <span class="price">{{getToDecimal2Price(item.normPrice)}}</span><span
                  class="submit-demand" @click="_addShoppingCar(item.drugDsId,1,item.normPrice)"></span>
                </div>
              </div>
            </li>
            <loading v-show="hasMore" title=""></loading>
          </ul>
        </div>
      </scroll>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Header2 from 'components/header/header2'
  import HosFilter from 'components/filter/HosFilter'
  import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import { toDecimal2 } from 'common/js/numutil'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import { addShoppingCar, getShoppingCarList } from 'api/shopping'
  import { getSortDetail } from 'api/sortdetail'
  const LOAD_SIZE = 10
  export default {
    created() {
      this._getSortDetail()
    },
    computed: {
      ...mapGetters(['drug'])
    },
    data() {
      return {
        drugList: [],
        hasMore: true
      }
    },
    components: {
      Header2,
      HosFilter,
      Scroll,
      Loading
    },
    methods: {
      _getSortDetail() {
        this.pageNum = 1
        getSortDetail('', this.drug.configId, '', '', '', '', this.pageNum, LOAD_SIZE).then((response) => {
          if (response.code === 1) {
            this.drugList = response.data
            this._checkMore(response.data)
          }
        })
      },
      searchMore() {
        if (!this.hasMore) {
          return
        }
        this.pageNum++
        getSortDetail('', this.drug.configId, '', '', '', '', this.pageNum, LOAD_SIZE).then((response) => {
          if (response.code === 1) {
            this.drugList = this.drugList.concat(response.data)
            this._checkMore(response.data)
          }
        })
      },
      _addShoppingCar(drugId, quantity, price) {
        addShoppingCar(drugId, quantity, price).then((response) => {
          if (response.code === 1) {
            console.log(',')
            getShoppingCarList().then((response) => {
              if (response.code === 1) {
                this.setNewShoppingCarList(response.data)
              }
            })
          }
        })
      },
      _checkMore(data) {
        if (data && data.length === LOAD_SIZE) {
          this.hasMore = true
        } else {
          this.hasMore = false
        }
      },
      getToDecimal2Price(num) {
        return toDecimal2(num)
      },
      ...mapMutations({
        setShoppingCarList: 'SET_SHOPPING_CAR_LIST'
      }),
      ...mapActions([
        'setNewShoppingCarList'
      ])
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .sort-detail
    position: fixed
    height: 100%
    width: 100%
    left: 0
    top: 0
    z-index: 10
    .drug-list
      width: 100%
      position: absolute
      top: 80px
      bottom: 0
      background: #fff
      overflow: hidden
      .drug-info-wrapper
        width: 100%
        height: 100px
        display: flex
        box-sizing: border-box
        padding: 0 10px
        border-1px(#DBD8D8, 10px)
        .avatar-wrapper
          flex: 0 0 60px
          margin: 10px 0
        .content-wrapper
          flex: 1
          margin: 10px 0 10px 10px
          .title
            line-height: 15px
            height: 30px
            font-size: 15px
            font-weight: 500
            color: #363636
            text-overflow: -o-ellipsis-lastline
            overflow: hidden
            text-overflow: ellipsis
            display: -webkit-box
            -webkit-line-clamp: 2
            -webkit-box-orient: vertical
          .des
            height: 13px
            font-size: 13px
            line-height: 13px
            color: #A2A2A2
            width: 200px
            margin-top: 7px
            white-space: nowrap
            overflow: hidden
            text-overflow: ellipsis
          .extra-content
            position: relative
            color: #F46A75
            margin-top: 10px
            height: 23px
            line-height: 23px
            font-size: 15px
            .submit-demand
              position: absolute
              right: 3px
              height: 23px
              border-radius: 3px
              padding: 0 3px
              border: 1px solid #F46A75
              box-sizing: border-box

  .slide-enter-active, .slide-leave-active
    transition: all 0.3s

  .slide-enter, .slide-leave-to
    transform: translate3d(100%, 0, 0)

</style>



