<template>
  <div>
    <div class="hybrid">
      <div class="qualification">
        <span class="qualification-text"></span>
      </div>
      <div class="select-item">
        <ul>
          <li v-for="(item,index) in filterData.showData" class="select-item-li"
              :class="{'selected':selectPosition1===index}"
              @click="clickItemAction(item,index)">
            <div>
              <span>{{item.key}}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      filterData: {
        type: Object
      },
      filterPosition: {
        type: Number
      },
      selectPosition: {
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        selectPosition1: this.selectPosition
      }
    },
    methods: {
      clickItemAction(item, index) {
        this.selectPosition1 = index
        this.$emit('clickItemAction', item, index, this.filterData.showType, this.filterPosition)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .hybrid
    background: #fff
    width: 100%
    height: 100px
    padding: 20px
    .qualification
      height: 40px
      .qualification-text
        font-size: 15px
        font-weight: 500
    .select-item
      margin-left: 15px
      .select-item-li
        font-size: 15px
        font-weight: 500
        padding: 5px
        display: inline-block
        margin-left: 10px
        &.selected
          color: #0294DF
          border: 1px solid #0294DF
          border-radius: 10%
</style>



