<template>
  <div class="scroll-refresh-header" ref="scrollRefreshHeader">
    <div class="pull-down-up-wrapper" ref="pullDownUpWrapper">
      <img width="24" height="24" src="./refresh_icon_arrow.png" class="arrow" ref="arrow">
      <span ref="downPullRefreshText" class="down-pull-refresh-text"></span>
    </div>
    <img width="24" height="24" src="./loading.gif" class="progress" ref="progress">
  </div>
</template>

<script type="text/ecmascript-6">
  const TAG = 'refreshheader '
  export default {
    props: {
      downPullY: {
        type: Number
      }
    },
    methods: {
      onRefreshCompleted() {
        this.progress.style.display = 'none'
      }
    },
    mounted() {
      this.scrollRefreshHeader = this.$refs.scrollRefreshHeader
      this.pullDownUpWrapper = this.$refs.pullDownUpWrapper
      this.refreshText = this.$refs.downPullRefreshText
      this.arrow = this.$refs.arrow
      this.progress = this.$refs.progress
    },
    watch: {
      downPullY(newVal, oldVal) {
        console.log(TAG + 'new ......' + newVal)
        if (newVal === 0) {
          this.pullDownUpWrapper.style.display = 'none'
          this.refreshText.firstChild.nodeValue = ''
          this.arrow.style.transform = ''
          this.arrow.style.webkitTransform = ''
          if (this.isCanRefresh) {
            this.isCanRefresh = false
            this.progress.style.display = 'inline'
            this.$emit('onRefresh')
          }
        } else if (newVal > 0) {
          this.pullDownUpWrapper.style.display = 'inline'
          if (newVal > 30) {
            this.isCanRefresh = true
            this.refreshText.firstChild.nodeValue = ''
            this.arrow.style.transform = 'rotate(180deg)'
            this.arrow.style.webkitTransform = 'rotate(180deg)'
          }
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .scroll-refresh-header
    width: 100%
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    .pull-down-up-wrapper
      padding: 10px 0
      color: #9C9C9C
      display: none
    .progress
      padding: 10px 0
      display: none


</style>



