import { commonParams, options } from './config'
import Vue from 'vue'
export function addShoppingCar(drugId, quantity, price) {
  const url = 'api/cart/addcartitem.htm'
  const params = Object.assign({}, commonParams, {
    drugId: drugId,
    quantity: quantity,
    price: price
  })
  return Vue.http.post(url, params, options).then(
    (successResponse) => {
      return Promise.resolve(successResponse.body)
    },
    (errorResponse) => {
      return Promise.reject(errorResponse)
    }
  )
}

export function getShoppingCarList() {
  const url = 'api/cart/findCartItemList.htm'
  const params = Object.assign({}, commonParams, {})
  return Vue.http.post(url, params, options).then(
    (successResponse) => {
      return Promise.resolve(successResponse.body)
    },
    (errorResponse) => {
      return Promise.reject(errorResponse)
    }
  )
}



