<template>
  <div class="onlinepharmacy-wrapper" ref="onlinepharmacyWrapper">
    <scroll ref="scroll" class="scroll-content" :data="recommendConfigList"
            :listenScroll="true" :pullup="true" :beforeScroll="true">
      <div>
        <refresh-header :downPullY="mDownPullY" @onRefresh="_getHomePageData()" ref="refreshHeader"></refresh-header>
        <!--<swiper :options='swiperOption' class='swiper-box' ref="mySwiper">-->
        <!--<swiper-slide class='swiper-item' v-for='(item,index) in rollImageConfigList' :key="index.id">-->
        <!--<a href="http://www.baidu.com">-->
        <!--<img class="needsclick" @load="loadImage" :src="item.imagePath" width="100%" height="120">-->
        <!--</a>-->
        <!--</swiper-slide>-->
        <!--<div class='swiper-pagination' slot='pagination'></div>-->
        <!--</swiper>-->
        <div v-if="rollImageConfigList.length" class="slider-wrapper" ref="sliderWrapper">
          <slider>
            <div v-for="item in rollImageConfigList">
              <a href="http://www.baidu.com">
                <img class="needsclick" @load="loadImage" :src="item.imagePath" width="100%" height="120">
              </a>
            </div>
          </slider>
        </div>
        <div class="classes-config-wrapper">
          <ul>
            <li v-for="parentIndex in classesConfigLength/4">
              <ul class="ul-classes-row-wrapper">
                <li v-for="(item,index) in getClassConfigByRow(parentIndex-1)" class="li-item-classes-config"
                    @click="_intoSortDetail(item)">
                  <div class="item-classes-config" v-if="item">
                    <img v-lazy="item.imagePath" width="50" height="50"/>
                    <span class="item-title">{{item.name}}</span>
                  </div>
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <oneline></oneline>
        <div class="up-down-scroll-wrapper">
          <img src="../../assets/pharmacy_roll_title.png" width="40" height="15" class="icon"/><span class="title"></span>
        </div>
        <split></split>
        <div class="special-config-wrapper">
          <ul class="ul-special-config-wrapper">
            <li v-for="item in specailConfigList" class="special-item-wrapper">
              <a>
                <img :src="item.imagePath" height="110px" width="100%"/>
              </a>
            </li>
          </ul>
        </div>
        <split></split>
        <div class="recommend-config-wrapper">
          <div class="header">
            <img src="../../assets/pharmacy_icon_recommend.png" width="20" height="20"/><span class="title"></span>
          </div>
          <oneline></oneline>
          <div class="content">
            <ul>
              <li v-for="parentIndex in Math.ceil(recommendConfigLength/3)">
                <ul class="ul-classes-row-wrapper">
                  <li v-for="(item,index) in getRecommendConfigByRow(parentIndex-1)" class="li-item-classes-config">
                    <div class="item-classes-config" v-if="item">
                      <div class="border-line-bottom"></div>
                      <div class="border-line-right" :class="{'current':index === 2}"></div>
                      <img v-lazy="item.imagePath" width="80" height="70"/>
                      <span class="item-title">{{item.standardName}}</span>
                      <span class="item-price">{{getToDecimal2Price(item.normPrice)}}</span>
                    </div>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type='text/ecmascript-6'>
  import Split from 'base/split/split.vue'
  import Oneline from 'base/split/1pxline.vue'
  import Slider from 'base/slider/slider'
  import Scroll from 'base/scroll/scroll'
  import RefreshHeader from 'base/scroll/refreshheader'
  import { toDecimal2 } from 'common/js/numutil'
  //  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import { mapMutations } from 'vuex'
  import { getHomePageData } from 'api/onlinepharmacy'
  const TAG = 'onlinepharmacy '
  const scrollImg = {
    'configId': '82ea270a94124567bdaa1d8fdfd829c3',
    'imagePath': 'https://ebao-zsyd.ebaonet.cn/uploadimages/f0cdfeb0e8754d45891150874f878c65',
    'jumpType': 2,
    'jumpTypeVal': '',
    'name': '2017'
  }
  export default {
    components: {
//      swiper,
//      swiperSlide,
      Slider,
      Scroll,
      Split,
      Oneline,
      RefreshHeader
    },
    data() {
      return {
//        swiperOption: {
//          // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
//          // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
//          notNextTick: true,
//          // swiper configs swiperapi
//          autoplay: 3000,
//          // direction : 'vertical',
//          effect: 'coverflow',
//          loop: true,
//          grabCursor: true,
//          setWrapperSize: true,
//          // autoHeight: true,
//          // paginationType:"bullets",
//          pagination: '.swiper-pagination',
//          paginationClickable: true,
//          prevButton: '.swiper-button-prev',
//          nextButton: '.swiper-button-next',
//          // scrollbar:'.swiper-scrollbar',
//          // mousewheelControl : true,
//          observeParents: true,
//          // if you need use plugins in the swiper, you can config in here like this
//          // debugger
//          // debugger: true,
//          // swiper callbacks
//          // swiperswiper
//          onTransitionStart(swiper) {
//            console.log(swiper)
//          }
//          // more Swiper configs and callbacks...
//          // ...
//        },
        mDownPullY: 0,
        rollImageConfigList: [],
        classesConfigList: [],
        specailConfigList: [],
        classesConfigLength: 0,
        recommendConfigList: [],
        recommendConfigLength: 0
      }
    },
    mounted() {
//      require('swiper/dist/css/swiper.css')
      setTimeout(() => {
        let scroll = this.$refs.scroll
        scroll.$on('scroll', (pos) => {
          console.log(TAG + 'scroll....' + pos.y)
          this.mDownPullY = pos.y
        })
        scroll.$on('scrollToHeader', () => {
          this.mDownPullY = 0
        })
        scroll.$on('scrollToEnd', () => {
        })
      }, 200)
      this.refreshHeader = this.$refs.refreshHeader
    },
    created() {
      console.log(TAG + 'created')
      this._getHomePageData()
    },
    methods: {
      showAlert() {
        alert('Hello......Hello.......Helllolol')
      },
      loadImage() {
        if (!this.checkloaded) {
          this.checkloaded = true
          this.$refs.scroll.refresh()
        }
      },
      _getHomePageData() {
        getHomePageData().then(
          (response) => {
            if (response.code === 1) {
              this.rollImageConfigList = response.data.rollImageConfigList
              this.rollImageConfigList.push(scrollImg)
              this.classesConfigList = response.data.classesConfigList
              this.classesConfigLength = response.data.classesConfigList.length
              this.specailConfigList = response.data.specailConfigList
              this.recommendConfigList = response.data.recommendConfigList
              this.recommendConfigLength = response.data.recommendConfigList.length
              console.log('recommendConfigLength===' + this.recommendConfigLength)
              setTimeout(() => {
                this.refreshHeader.onRefreshCompleted()
                this.$refs.scroll.refresh()
              }, 2000)
            }
          }
        )
      },
      getClassConfigByRow(row) {
        let classes = []
        for (let i = row * 4; i < (row + 1) * 4; i++) {
          classes.push(this.classesConfigList[i])
        }
        return classes
      },
      getRecommendConfigByRow(row) {
        let classes = []
        for (let i = row * 3; i < (row + 1) * 3; i++) {
          classes.push(this.recommendConfigList[i])
        }
        return classes
      },
      getToDecimal2Price(num) {
        return toDecimal2(num)
      },
      _intoSortDetail(item) {
        console.log(TAG + '_intoSortDetail')
        this.$router.push({
          path: `/onlinepharmacy/${item.configId}`
        })
        this.setDrug(item)
      },
      ...mapMutations({
        setDrug: 'SET_DRUG'
      })
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    activated() {
//      this.$refs.scroll.refresh()
//      console.log(TAG + 'activated')
    }
  }
</script>

<style lang='stylus' rel='stylesheet/stylus'>
  .onlinepharmacy-wrapper
    .scroll-content
      position: fixed
      top: 0
      left: 0
      bottom: 48px
      width: 100%
      .swiper-box
        width: 100%
        height: 120px
      .slider-wrapper
        width: 100%
        overflow: hidden
        position: relative
      .classes-config-wrapper
        .ul-classes-row-wrapper
          display: flex
          .li-item-classes-config
            flex: 1
            .item-classes-config
              display: flex
              width: 100%
              height: 90px
              flex-direction: column
              justify-content: center
              align-items: center
              .item-title
                margin-top: 10px
                font-size: 10px
                color: #494848
      .up-down-scroll-wrapper
        width: 100%
        height: 40px
        padding: 0 10px
        display: flex
        align-items: center
        .title
          font-size: 10px
          margin-left: 15px
          color: #A4A1A1
      .special-config-wrapper
        .ul-special-config-wrapper
          width: 100%
          height: 110px
          display: flex
          .special-item-wrapper
            flex: 1
      .recommend-config-wrapper
        .header
          padding: 8px 0
          display: flex
          flex-direction: row
          justify-content: center
          align-items: center
          .title
            height: 20px
            line-height: 20px
            margin-left: 10px
            color: #00A5F9
            font-size: 12px
        .content
          .ul-classes-row-wrapper
            display: flex
            font-size: 0
            .li-item-classes-config
              flex: 1
              position: relative
              .border-line-bottom
                position: absolute
                bottom: 0
                left: 10px
                right: 10px
                height: 1px
                background: #f4f4f4
              .border-line-right
                position: absolute
                bottom: 10px
                top: 10px
                right: 0
                width: 1px
                background: #f4f4f4
                &.current
                  display: none
              .item-classes-config
                display: flex
                width: 100%
                height: 160px
                flex-direction: column
                justify-content: center
                align-items: center
                .item-title
                  margin: 10px 5px
                  font-size: 15px
                  line-height: 17px
                  color: #494848
                  text-overflow: -o-ellipsis-lastline
                  overflow: hidden
                  text-overflow: ellipsis
                  display: -webkit-box
                  -webkit-line-clamp: 2
                  -webkit-box-orient: vertical
                .item-price
                  color: #FF7A79
                  font-size: 15px
</style>



