<template>
  <div class="demand-list">
    <header1 :title="''" :showBackIcon="false" :rightText="''"></header1>
    <scroll ref="scroll" class="shopping-list-wrapper" :data="shoppingCarList">
      <div class="shopping-list-content">
        <ul>
          <li v-for="(item,parentIndex) in shoppingCarList" class="parent-list-wrapper">
            <div class="list-header">
              <span class="selector-parent" :class="{'checked':isAllSelected}"
                    @click="setShoppingCarAllSelect"></span><span
              class="avatar"></span><span
              class="drug-shop-name">{{item.drugStoreName}}</span><span class="distribution">28.0</span>
            </div>
            <oneline></oneline>
            <ul>
              <li v-for="(childItem,childDrugIndex) in item.drugList">
                <div class="list-content">
                  <div class="selector-wrapper">
                    <span class="selector-parent" :class="{'checked':childItem.checked}"
                          @click="setShoppingItemChecked({parentIndex:parentIndex, itemIndex:childDrugIndex})"></span>
                  </div>
                  <img v-lazy="childItem.picUrl" width="70" height="80"/>
                  <div class="content">
                    <span class="title">{{childItem.displayName}}</span>
                    <span class="quality">{{childItem.norms}}</span>
                    <div class="count-wrapper">
                      <span class="price">{{childItem.curPrice}}</span>
                      <span class="plus">8888</span>
                    </div>
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <div class="loading-container" v-show="!shoppingCarList.length">
        <loading></loading>
      </div>
    </scroll>
    <div class="shopping-submit-order">
      <div class="total-wrapper">
        <span class="total-text"></span><span class="total-price">381.00</span>
      </div>
      <span class="action-submit-order"></span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Header1 from 'components/header/header1'
  import Scroll from 'base/scroll/scroll'
  import Oneline from 'base/split/1pxline.vue'
  import Loading from 'base/loading/loading'
  import { getShoppingCarList } from 'api/shopping'
  import { mapMutations, mapGetters, mapActions } from 'vuex'
  const TAG = 'demandlist '
  export default {
    data() {
      return {
//        shoppingList: []
      }
    },
    beforeCreate() {
      console.log(TAG + 'beforeCreate')
    },
    created() {
      console.log(TAG + 'created')
      this._getShoppingCarList()
    },
    methods: {
      _getShoppingCarList() {
        getShoppingCarList().then((response) => {
          if (response.code === 1) {
//            this.shoppingList = response.data
            this.setNewShoppingCarList(response.data)
          }
        })
      },
      ...mapMutations({
        setShoppingCarList: 'SET_SHOPPING_CAR_LIST'
      }),
      ...mapActions([
        'setShoppingItemChecked',
        'setNewShoppingCarList',
        'setShoppingCarAllSelect'
      ])
    },
    computed: {
      ...mapGetters(['shoppingCarList', 'isAllSelected'])
    },
    components: {
      Header1,
      Scroll,
      Oneline,
      Loading
    },
    beforeMount() {
      console.log(TAG + 'beforeMount')
    },
    mounted() {
      console.log(TAG + 'mounted')
    },
    beforeUpdate() {
      console.log(TAG + 'beforeUpdate')
    },
    updated() {
      console.log(TAG + 'updated')
    },
    activated() {
      this.$refs.scroll.refresh()
      console.log(TAG + 'activated')
    },
    deactivated() {
      console.log(TAG + 'deactivated')
    },
    beforeDestroy() {
      console.log(TAG + 'beforeDestroy')
    },
    destroyed() {
      console.log(TAG + 'destroyed')
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/mixin.styl"
  .demand-list
    position: fixed
    top: 0
    bottom: 48px
    left: 0
    width: 100%
    .shopping-list-wrapper
      position: absolute
      top: 40px
      left: 0
      bottom: 40px
      width: 100%
      background: #f4f4f4
      .shopping-list-content
        background: #ffffff
        .parent-list-wrapper
          width: 100%
          .list-header
            width: 100%
            height: 40px
            padding: 10px
            position: relative
            box-sizing: border-box
            display: flex
            align-items: center
            .selector-parent
              display: inline-block
              width: 16px
              height: 16px
              background-size: 16px 16px
              background-repeat: no-repeat
              bg-image('pharmacy_shopping_icon_unselected')
              &.checked
                bg-image('pharmacy_shopping_icon_selected')
            .avatar
              margin-left: 5px
              display: inline-block
              width: 16px
              height: 16px
              background-size: 16px 16px
              background-repeat: no-repeat
              bg-image('pharmacy_shopping_icon_pharmacy')
            .drug-shop-name
              margin-left: 5px
              font-size: 15px
            .distribution
              position: absolute
              right: 10px
              top: 12.5px
              font-size: 15px
              height: 15px
          .list-content
            width: 100%
            height: 100px
            padding: 10px
            box-sizing: border-box
            position: relative
            display: flex
            border-1px(#DBD8D8, 10px)
            .selector-wrapper
              height: 80px
              display: flex
              align-items: center
              margin-right: 5px
              .selector-parent
                display: inline-block
                width: 16px
                height: 16px
                background-size: 16px 16px
                background-repeat: no-repeat
                bg-image('pharmacy_shopping_icon_unselected')
                &.checked
                  bg-image('pharmacy_shopping_icon_selected')
            .content
              height: 80px
              margin-left: 5px
              position: relative
              flex: 1
              .title
                line-height: 15px
                height: 30px
                font-size: 15px
                font-weight: 500
                color: #363636
                text-overflow: -o-ellipsis-lastline
                overflow: hidden
                text-overflow: ellipsis
                display: -webkit-box
                -webkit-line-clamp: 2
                -webkit-box-orient: vertical
              .quality
                display: inline-block
                font-size: 15px
                color: #AAAAAA
                margin-top: 5px
              .count-wrapper
                position: absolute
                bottom: 0
                left: 0
                right: 0
                height: 20px
                line-height: 20px
                color: #FF6968
                .plus
                  position: absolute
                  right: 0
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
    .shopping-submit-order
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      height: 40px
      background: #ffffff
      display: flex
      align-items: center
      .total-wrapper
        display: inline-block
        margin-left: 10px
        flex: 1
        .total-price
          color: #F66F6B
          margin-left: 5px
      .action-submit-order
        display: inline-block
        height: 40px
        line-height: 40px
        text-align: center
        width: 100px
        background: #F66F6B
        color: #fff
        font-size: 15px

</style>



