import { commonParams, options } from './config'
import Vue from 'vue'
export function getLevelOne() {
  const url = 'api/cate/catelevelone.htm'
  const params = Object.assign({}, commonParams, {})
  return Vue.http.post(url, params, options).then(
    (successResponse) => {
      return Promise.resolve(successResponse.body)
    },
    (errorResponse) => {
      return Promise.reject(errorResponse)
    }
  )
}

export function getLevelDetail(cateId, start, length) {
  const url = 'api/cate/cateleveltwo.htm'
  const params = Object.assign({}, commonParams, {
    cateLevelOneId: cateId,
    start: start,
    length: length
  })
  return Vue.http.post(url, params, options).then(
    (successResponse) => {
      return Promise.resolve(successResponse.body)
    },
    (errorResponse) => {
      return Promise.reject(errorResponse)
    }
  )
}



