import { addShoppingCar, getShoppingCarList } from 'api/shopping'
import * as types from './mutation-types'
import Vue from 'vue'
/**
 * actionpromise
 */
export const addShoppingCarList = function ({commit}, drugId, quantity, price) {
  addShoppingCar(drugId, quantity, price).then((response) => {
    if (response.code === 1) {
      console.log('')
      getShoppingCarList().then((response) => {
        if (response.code === 1) {
          commit(types.SET_SHOPPING_CAR_LIST, response.data)
        }
      })
    }
  })
}
/**
 * checked
 * @param commit
 * @param state
 * @param parentIndex
 * @param itemIndex
 */
export function setShoppingItemChecked({commit, state}, {parentIndex, itemIndex}) {
  // sliceVuexVuexmutation
  let drugList = JSON.parse(JSON.stringify(state.shoppingCarList.slice()))
  let childItem = drugList[parentIndex].drugList[itemIndex]

  childItem.checked = !childItem.checked
  commit(types.SET_IS_ALL_SELECTED, judgeIsAllSelect(drugList))
  commit(types.SET_SHOPPING_CAR_LIST, drugList)
}
/**
 * checked
 * @param commit
 * @param state
 * @param shoppingCarList
 */
export function setNewShoppingCarList({commit, state}, shoppingCarList) {
  let drugList = state.shoppingCarList.slice()
  shoppingCarList.forEach((parent) => {
    parent.drugList.forEach((child) => {
      // checked
      Vue.set(child, 'checked', childIsChecked(drugList, child))
    })
  })
  commit(types.SET_SHOPPING_CAR_LIST, shoppingCarList)
}
/**
 * 
 * @param commit
 * @param state
 * @param isAllSelect 
 */
export function setShoppingCarAllSelect({commit, state}) {
  let drugList = JSON.parse(JSON.stringify(state.shoppingCarList.slice()))
  let isAllSelect = !state.isAllSelected
  drugList.forEach((parent) => {
    parent.drugList.forEach((child) => {
      child.checked = isAllSelect
    })
  })
  commit(types.SET_IS_ALL_SELECTED, isAllSelect)
  commit(types.SET_SHOPPING_CAR_LIST, drugList)
}

/**
 *
 * @param list Vuexstate
 * @param drug drug
 * @returns {*}
 */
function childIsChecked(list, drug) {
  let drugStoreList = list.find((parent) => {
    return parent.drugStoreId === drug.drugStoreId
  })
  if (drugStoreList && drugStoreList.drugList) {
    let childDrug = drugStoreList.drugList.find((child) => {
      return child.drugDsId === drug.drugDsId
    })
    if (childDrug) {
      return childDrug.checked
    }
  }
  return false
}
/**
 * 
 * @param list
 */
function judgeIsAllSelect(list) {
  let drugShopList = list.find((parent) => {
    let drug = parent.drugList.find((child) => {
      return child.checked === false
    })
    return drug
  })
  if (drugShopList) {
    return false
  }
  return true
}



