import Vue from 'vue'
import Router from 'vue-router'
import Onlinepharmacy from 'components/onlinepharmacy/onlinepharmacy'
import Drugsort from 'components/drugsort/drugsort'
import Demandlist from 'components/demandlist/demandlist'
import Ordermanager from 'components/ordermanager/ordermanager'
import SortDetail from 'components/sortdetail/sortdetail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/onlinepharmacy'
    },
    {
      path: '/onlinepharmacy',
      component: Onlinepharmacy,
      children: [
        {
          path: ':id',
          component: SortDetail
        }
      ]
    },
    {
      path: '/drugsort',
      component: Drugsort
    },
    {
      path: '/demandlist',
      component: Demandlist
    },
    {
      path: '/ordermanager',
      component: Ordermanager
    }
  ]
})



